import './Oceans.css';
import Toolbar from '../../components/Toolbar/Toolbar';
import React, { Component } from 'react';
import SideDrawer from '../../components/SideDrawer/SideDrawer';
import Backdrop from '../../components/Backdrop/Backdrop';
import SideMenuOcean from '../../components/SideMenuOcean/SideMenuOcean';
import OceanBg from '../../components/OceanBg/OceanBg';
import styled from 'styled-components';
import InfoAquaticlife from '../../components/InfoAquaticlife/InfoAquaticlife';
import InfoAcidification from '../../components/InfoAcidification/InfoAcidification';
import InfoSealevel from '../../components/InfoSealevel/InfoSealevel';
import InfoOceanwarming from '../../components/InfoOceanwarming/InfoOceanwarming';
import InfoOverfishing from '../../components/InfoOverfishing/InfoOverfishing';
import InfoHypoxia from '../../components/InfoHypoxia/InfoHypoxia';
import InfoPollution from '../../components/InfoPollution/InfoPollution';
import AskOceans from '../../components/AskOceans/AskOceans';
import {Link} from 'react-router-dom';

const Tree = styled.p`
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  font-family:roboto;
  font-size: 1.5rem;
  font-weight:300;
  color: #ffffff;
  margin: 0em 0em 0.9em;
  letter-spacing: .6px;
  margin-left:0px;
  
`;

const Stacker= styled.div`
  display: flex;
  flex-direction:row;
  height: auto;
  justify-content: space-around;
  @media (max-width: 1130px) {
    flex-direction: column;
  }
`;
const Left = styled.div`
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display:flex;
  justify-content:center;
`;

const Threat = styled.div`
    width:40vw;
  align-self:center;
  @media (max-width: 1130px) {
    width:85%;
  }
   @media (max-width: 768px) {
    width:100%;
  }
`;

const Right = styled.div`
    display:flex;
  justify-content:center;
`;

const Impacts = styled.div`
  width:40vw;
  flex-direction:column;
  justify-content:center;

    @media (max-width: 1130px) {
    width:85%;
  }
   @media (max-width: 768px) {
    width:100%;
  }
`;

const Contentwrapper = styled.div`
  margin-left:15px;
  margin-top:15px;
  margin-right:15px;
  margin-bottom:15px;

`;

const Impactwrapper = styled.div`
  margin-left:0px;
  margin-top:15px;
  margin-right:0px;
  margin-bottom:15px;

`;



class Oceans extends Component{


  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){

   
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }
    return(
      
        <body> 
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuOcean />
          <OceanBg/>
          
          <main className= "bod">

          <Stacker>
            
      <Contentwrapper>
          <Left>
           <Threat>  
           <Contentwrapper>
          <Tree>Oceans/</Tree>         
          </Contentwrapper>        
              <AskOceans/>
           </Threat>
          </Left>
      </Contentwrapper>
     
      <Contentwrapper>
          <Right>
           <Impacts>
 
              <Impactwrapper><Link to="/Oceans_Aquatic-life"><InfoAquaticlife/></Link></Impactwrapper>

              <Impactwrapper><Link to="/Oceans_Acidification"><InfoAcidification/></Link></Impactwrapper> 
              <Impactwrapper><Link to="/Oceans"><InfoOceanwarming/></Link></Impactwrapper>  
                  
              <Impactwrapper><Link to="/Oceans_Sea-level"><InfoSealevel/></Link></Impactwrapper>
              <Impactwrapper><Link to="/Oceans"><InfoOverfishing/></Link></Impactwrapper>      
              <Impactwrapper><Link to="/Oceans"><InfoHypoxia/></Link></Impactwrapper> 
              <Impactwrapper><Link to="/Oceans_Plastic-pollution"><InfoPollution/></Link></Impactwrapper>
           </Impacts>
          </Right> 
      </Contentwrapper>

      </Stacker>
                  
          </main>
        </body>
      
      );

  } 
}

export default Oceans;