class Ocean_Arcticicemin extends Component{


  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){

   
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }
    return(
      
        <body> 
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuOcean />
          <OceanBg/>
          
          <main className = "bod">

          <Contentwrapper>
          <Tree><Link to="/Oceans"><Backico src= {back}/></Link><Link to="/Oceans" className="link">Oceans </Link>/ Arctic Ice Minimum</Tree>
          </Contentwrapper>
          
          <Upper>
          
          <Contentwrapper>
          
            <Threat>          
              <CardArcticicemin/>
            </Threat>
          </Contentwrapper>

          <Impacts>
            <Contentwrapper><a href= "/"><InfoFlatworm/></a></Contentwrapper>
            <Contentwrapper><a href= "/"><InfoSeaturtle/></a></Contentwrapper>
            
          </Impacts>
          </Upper>

          
          

         
                  
          </main>
        </body>
      
      );

  } 
}

