
//import logo from './logo.svg';
import './App.css';
import ScrollToTop from 'react-router-scroll-top'
import Toolbar from './components/Toolbar/Toolbar';
import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import SideDrawer from './components/SideDrawer/SideDrawer';
import Backdrop from './components/Backdrop/Backdrop';
import styled from 'styled-components';
import VitalDeforestation from './components/VitalDeforestation/VitalDeforestation';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import Ocean_Aquaticlife from './components/Ocean_Aquaticlife/Ocean_Aquaticlife';

import Ocean_SeaLevel from './components/Ocean_SeaLevel/Ocean_SeaLevel';
import Ocean_Acidification from './components/Ocean_Acidification/Ocean_Acidification';
import Ocean_Arcticicemin from './components/Ocean_Arcticicemin/Ocean_Arcticicemin';
import Ocean_Plasticpollution from './components/Ocean_Plasticpollution/Ocean_Plasticpollution';
import Ocean_Lionfish from './components/Ocean_Lionfish/Ocean_Lionfish';
import Ocean_Coral from './components/Ocean_Coral/Ocean_Coral';
import Ocean_Seaturtle from './components/Ocean_Seaturtle/Ocean_Seaturtle';
import Land_Deforestation from './components/Land_Deforestation/Land_Deforestation';
import Land_Forestfires from './components/Land_Forestfires/Land_Forestfires';
import HomeBg from './components/HomeBg/HomeBg';
import SideMenuHome from './components/SideMenuHome/SideMenuHome';
import Oceans from './components/Oceans/Oceans';
import Land from './components/Land/Land';
import Atmosphere from './components/Atmosphere/Atmosphere';
import NearSpace from './components/NearSpace/NearSpace';
import VitalNearspace from './components/VitalNearspace/VitalNearspace';
import VitalAir from './components/VitalAir/VitalAir';
import VitalLand from './components/VitalLand/VitalLand';
import VitalOceans from './components/VitalOceans/VitalOceans';
import InfoEarthnight from './components/InfoEarthnight/InfoEarthnight';


const Tree = styled.p`
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  font-family:roboto;
  font-size: 1.5rem;
  font-weight:300;
  color: #ffffff;
  margin: 0em 0em 0.9em;
  letter-spacing: .6px;
  margin-left:0px;
  
`;

const Stacker= styled.div`
  display: flex;
  flex-direction:row;
  height: auto;
  justify-content: space-around;
  @media (max-width: 1130px) {
    flex-direction: column;
  }
`;
const Left = styled.div`
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display:flex;
  justify-content:center;
`;

const Threat = styled.div`
    width:40vw;
  align-self:center;
  @media (max-width: 1130px) {
    width:85%;
  }
   @media (max-width: 768px) {
    width:100%;
  }
`;

const Right = styled.div`
    display:flex;
  justify-content:center;
`;

const Impacts = styled.div`
  width:40vw;
  flex-direction:column;
  justify-content:center;

    @media (max-width: 1130px) {
    width:85%;
  }
   @media (max-width: 768px) {
    width:100%;
  }
`;

const Contentwrapper = styled.div`
  margin-left:15px;
  margin-top:15px;
  margin-right:15px;
  margin-bottom:15px;

`;

const Impactwrapper = styled.div`
  margin-left:0px;
  margin-top:15px;
  margin-right:0px;
  margin-bottom:15px;

`;








class App extends Component{


  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){

   
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }



    return(
      
      <Router>
      <ScrollToTop>
        <body> 
         
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuHome />
          <HomeBg/>
          <main className = "contentmain">

          <Switch>
              <Route path= "/" component={Home} exact/>
              <Route path= "/Oceans" component ={Oceans}/>
              <Route path= "/Oceans_Aquatic-life" component ={Ocean_Aquaticlife}/>
              <Route path= "/Oceans_Sea-Level" component={Ocean_SeaLevel}/>
              <Route path= "/Oceans_Acidification" component={Ocean_Acidification}/>
              <Route path= "/Oceans_Arctic-ice-minimum" component={Ocean_Arcticicemin}/>
              <Route path= "/Oceans_Plastic-pollution" component={Ocean_Plasticpollution}/>
              <Route path= "/Oceans_Lionfish" component={Ocean_Lionfish}/>
              <Route path= "/Oceans_Coral-reefs" component={Ocean_Coral}/>
              <Route path= "/Oceans_Sea-turtle" component={Ocean_Seaturtle}/>

              <Route path= "/Land" component= {Land}/>
              <Route path= "/Land_Deforestation" component={Land_Deforestation}/> 
              <Route path= "/Land_Forestfires" component={Land_Forestfires}/> 

              <Route path= "/Atmosphere" component={Atmosphere}/>  

              <Route path= "/Near-Space" component={NearSpace}/>     
          
          </Switch>

          </main>
        </body>
        </ScrollToTop>
      </Router>
      
      );

  } 
}


function Home() {
  return (
    <body> 
      <main className = "bod">
      
      <Stacker>
            
      <Contentwrapper>
          <Left>
           <Threat>  
           <Contentwrapper>
          <Tree>Home/</Tree>         
          </Contentwrapper>        
              <InfoEarthnight/>
           </Threat>
          </Left>
      </Contentwrapper>
     
      <Contentwrapper>
          <Right>
           <Impacts>
 
              <Impactwrapper><Link to="/Oceans"><VitalOceans/></Link></Impactwrapper>  
              <Impactwrapper><Link to="/Land"><VitalLand/></Link></Impactwrapper>     
              <Impactwrapper><Link to="/Atmosphere"><VitalAir/></Link></Impactwrapper>  
              <Impactwrapper><Link to="/Near-Space"><VitalNearspace/></Link></Impactwrapper>  

           </Impacts>
          </Right> 
      </Contentwrapper>

      </Stacker>
          

      </main>
    </body>
);
}

export default App;





